import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { OnboardingService } from '../../../onboarding.service';

@Component({
  selector: 'lib-dialog-create-onboarding',
  templateUrl: './dialog-create-onboarding.component.html',
  styleUrls: ['./dialog-create-onboarding.component.scss']
})
export class DialogCreateOnboardingComponent implements OnInit {
  templates = [];
  onboarding = {
    TemplateId: 0,
  }
  constructor(@Inject(MAT_DIALOG_DATA) private data: any,
    private dialogRef: MatDialogRef<DialogCreateOnboardingComponent>, private service: OnboardingService) {
    this.service._templates.subscribe((data) => {
      this.templates = data;
    });
  }

  ngOnInit(): void {
    this.templates = this.data.templates;
  }

  createOnboarding() {
    if (!this.disabled()) {
      const template = this.templates.find((temp) => temp._id === this.onboarding.TemplateId)
      this.dialogRef.close({
        ...this.onboarding, categories: template.categories
      })
    }
  }
  close() {
    this.dialogRef.close(false)
  }

  disabled() {
    return !(this.onboarding.TemplateId)
  }

}
